





















import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: "uni-notice-bar",
	components: {
		uniIcon
	},
	props: {
		text: String,
		moreText: String,
		backgroundColor: {
			type: String,
			default: '#fffbe8'
		},
		speed: { //默认1s滚动100px
			type: [String, Number],
			default: 50
		},
		color: {
			type: String,
			default: '#de8c17'
		},
		single: { //是否单行
			type: [String, Boolean],
			default: false
		},
		scrollable: { //是否滚动，添加后控制单行效果取消
			type: [String, Boolean],
			default: false
		},
		showIcon: { //是否显示左侧icon
			type: [String, Boolean],
			default: false
		},
		showClose: { //是否显示左侧关闭按钮
			type: [String, Boolean],
			default: false
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			show: true,
			dura: 1
		}
	},
	computed: {
		setTextClass() {
			let classList = []
			if (this.scrollable === true || this.scrollable === 'true') {
				classList.push('uni-noticebar--scrollable')
			} else {
				if (this.single === 'true' || this.single === true || this.moreText) {
					classList.push('uni-noticebar--single')
				}
			}
			return classList
		},
		setContenClass() {
			let classList = []
			if (this.scrollable === true || this.scrollable === 'true' || this.single === 'true' || this.single === true ||
				this.moreText) {
				classList.push('uni-noticebar--flex')
			}
			return classList
		}
	},











	onReady() {
		let view = uni.createSelectorQuery().select(`#${this.elId}`);
		view.fields({
			size: true
		}, data => {
			this.dura = data.width / this.speed
		}).exec();
	},

	methods: {
		close() {
			this.show = false
			this.$emit('close')
		},
		clickMore() {
			this.$emit('getmore')
		},
		onClick() {
			this.$emit('click')
		}
	}
}
